body {
  margin: 0;
}

h1,
h2 {
  font-family: "Playfair Display";
  text-transform: capitalize;
}

h3 {
  font-family: "Lato";
  text-transform: uppercase;
}

p,
li {
  font-family: "Lato";
}
p.signature,
li.signature {
  padding-top: 1rem;
  font-size: 2rem;
}

ul {
  list-style: none;
}

h1 {
  font-size: 4rem;
  font-weight: 400;
  text-align: center;
}

h2 {
  font-size: 3rem;
  font-weight: 400;
  text-align: center;
}

h3 {
  font-size: 1.25rem;
  font-weight: 300;
  text-align: center;
}

a {
  text-decoration: none;
  color: #2f5266;
}

main {
  height: 100%;
  width: 100%;
  position: relative;
}
main section {
  min-height: 100vh;
  min-height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5vh 0;
}
main section .content {
  max-width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.bouton-rdv {
  background-color: #2f5266;
  color: #f4f0ed;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: 18px;
  transition: opacity 0.3s;
}
.bouton-rdv-xl {
  font-size: 24px;
  background-color: #2f5266;
  color: #f4f0ed;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s;
}

.bouton-rdv:hover {
  opacity: 0.8;
}

.mobile {
  display: block;
}

.desktop {
  display: none;
}

@media (min-width: 768px) {
  h2 {
    margin-bottom: 10vh;
  }
  .mobile {
    display: none;
  }
  .desktop {
    display: block;
  }
  p.signature {
    font-size: 2rem;
  }
}
@media (min-width: 1200px) {
  h1 {
    font-size: 6vw;
  }
  h2 {
    font-size: 4vw;
    margin-bottom: 17vh;
  }
  p {
    font-size: 1.25rem;
  }
  p.signature {
    font-size: 2.5rem;
  }
}
@media (min-width: 2000px) {
  h3 {
    font-size: 3rem;
  }
  p {
    font-size: 2rem;
  }
  p.signature {
    font-size: 3.5rem;
  }
}
header {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 10;
  background-color: #2f5266;
  height: fit-content;
  width: 100%;
}
header #hamburger {
  cursor: pointer;
  margin: 15px 15px;
  display: block;
  height: fit-content;
  width: 25px;
}
header #hamburger .hamburger-bar {
  width: 100%;
  height: 3px;
  display: block;
  margin-bottom: 5px;
  background-color: rgb(255, 255, 255);
  transition: transform 0.3s ease-in-out;
}
header #hamburger .hamburger-bar:last-child {
  margin-bottom: 0;
}
header #hamburger.active .hamburger-bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
header #hamburger.active .hamburger-bar:nth-child(2) {
  opacity: 0;
}
header #hamburger.active .hamburger-bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}
header nav {
  display: none;
}
header nav ul {
  flex-direction: column;
  padding: 0 10px;
  margin: 0;
  list-style: none;
}
header nav ul li,
header nav ul .nav-label {
  margin: 10px 0;
  padding: 10px;
}
header nav ul li a,
header nav ul .nav-label a {
  display: block;
  text-decoration: none;
  color: #f4f0ed;
}
header nav ul li a:hover, header nav ul li a.active,
header nav ul .nav-label a:hover,
header nav ul .nav-label a.active {
  font-weight: bold;
  text-decoration: underline;
}
header .nav-open {
  display: flex;
  flex-direction: column;
  background-color: #2f5266;
}
header .nav-icons {
  display: flex;
  align-items: center;
}
header .nav-label {
  color: #f4f0ed;
  align-items: center;
  font-family: "Lato";
  padding-left: 20px;
  font-size: 1.25rem;
  margin: 20px 0;
}

@media (min-width: 768px) {
  header #hamburger {
    display: none;
  }
  header nav, header .nav-open {
    position: fixed;
    top: 0px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #2f5266;
    flex-direction: row;
  }
  header nav .nav-label, header .nav-open .nav-label {
    margin: 0;
    font-size: 18px;
  }
  header nav ul, header .nav-open ul {
    display: flex;
    flex-direction: row;
    padding: 0;
  }
  header nav ul li, header .nav-open ul li {
    margin: 0 10px;
    font-size: 18px;
  }
}
@media (min-width: 1200px) {
  header nav .nav-label {
    margin: 0;
    font-size: 24px;
  }
  header nav ul li {
    font-size: 24px;
  }
}
.hero {
  background-color: #2f5266;
  color: #f4f0ed;
  text-align: center;
  justify-content: space-between;
  padding: 0;
}
.hero .content {
  margin-top: 20vh;
}
.hero .logo-container {
  height: 50vh;
  width: 50vw;
  min-width: 350px;
  display: flex;
  align-items: end;
}
.hero .logo-container .logo {
  background-image: url("../svg/logo_black 1.svg");
  background-position: center bottom;
  background-size: contain;
  background-repeat: no-repeat;
  height: 80%;
  width: 100%;
}

.motifs {
  background-color: #f4f0ed;
  color: #2f5266;
}
.motifs .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.motifs .content .motif {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}
.motifs .content .motif svg {
  height: 10vw;
  min-height: 50px;
}
.motifs .content .motif p {
  margin-top: 5px;
  text-align: center;
}

@media (min-width: 768px) {
  .motifs .content {
    display: flex;
    flex-direction: row !important;
  }
  .motifs .content .motif {
    height: 35vh;
    justify-content: start;
  }
  .motifs .content .motif p {
    margin-top: 20px;
  }
}
@media (min-width: 1200px) {
  .motifs .content {
    display: flex;
    flex-direction: row !important;
    justify-content: space-evenly;
    width: 75vw;
  }
  .motifs .content .motif {
    margin: 0 2.5vw;
  }
  .motifs .content .motif p {
    margin-top: 20px;
  }
}
.about {
  background-color: #2f5266;
  color: #f4f0ed;
}
.about .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about .content .right {
  border-radius: 50%;
  border: solid 2px #dea681;
  width: 53vw;
  height: 53vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}
.about .content .right img {
  border-radius: 50%;
  object-fit: cover;
  width: 50vw;
  height: 50vw;
  border: solid #f4f0ed 2px;
}
.about .content .left p {
  text-align: center;
}
.about .content .left p.signature {
  text-align: right;
  font-family: "Great Vibes", cursive;
}

@media (min-width: 768px) {
  .about .content {
    flex-direction: row-reverse !important;
    justify-content: space-evenly !important;
    width: 75vw;
  }
  .about .content .right {
    width: 25vw;
    height: 25vw;
  }
  .about .content .right img {
    width: 24vw;
    height: 24vw;
  }
  .about .content .left {
    width: 35vw;
  }
}
.contact {
  background-color: #f4f0ed;
  color: #2f5266;
}
.contact .badges {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.contact .badge {
  border: 2px solid hsl(14, 33%, 90%);
  border-radius: 5px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 100px;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-top: 20px;
  min-height: 15vh;
}
.contact .badge .badge-svg {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.contact .badge .badge-svg svg {
  grid-column: 1;
  margin: 5px;
  object-fit: contain;
  width: 80%;
  max-height: 100%;
}
.contact .badge .badge-text {
  text-align: center;
}

@media (min-width: 768px) {
  .badge {
    width: 40vw;
  }
}
footer {
  background-color: #2f5266;
  color: #f4f0ed;
  padding: 5px;
  display: flex;
  flex-direction: column;
}
footer .footer-elements {
  display: flex;
  justify-content: space-around;
  padding-left: 10px;
}
footer .teleconsultation-badge {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 10px;
  border: 1px #f4f0ed solid;
  border-radius: 5px 5px 0 0;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
footer .teleconsultation-badge.visible {
  display: block;
}
footer .teleconsultation-badge img {
  padding: 5px;
  object-fit: contain;
  height: 5vh;
  text-align: center;
}

@media (min-width: 992px) {
  footer {
    padding: 5px;
  }
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}
.modal .modal-content {
  background-color: #f4f0ed;
  color: #2f5266;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}
.modal .modal-content ul {
  list-style: disc;
}
.modal .modal-content ul li {
  margin-top: 5px;
}
.modal .modal-content ul li a {
  text-decoration: underline;
  cursor: pointer;
}
.modal .modal-content ul li a:hover {
  font-weight: bold;
}
.modal .close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.modal .close:hover,
.modal .close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.trigger-modal {
  cursor: pointer;
}
.trigger-modal:hover {
  font-weight: bold;
}

